import { MOBILE_BREAKPOINT } from './Config';

export class BusinessesCards {

  cards;
  cardsContainer;
  cardsInner;
  tabs;
  tabsContainer;

  CLASS_CARD_ACTIVE = 'Businesses__cards-item--active';
  CLASS_TAB_ACTIVE = 'Businesses__tabs-item--active';
  SLIDESHOW_INTERVAL_DURATION = 7000;

  slideshowInterval;
  hasInteracted = false;

  constructor(){
    this.cardsContainer = document.querySelector('.Businesses__cards');
    this.cardsInner = document.querySelector('.Businesses__cards-cardsInner');
    this.cards = [...document.querySelectorAll('.Businesses__cards-item')];

    this.tabsContainer = document.querySelector('.Businesses__tabs');
    this.tabs = [...document.querySelectorAll('.Businesses__tabs-item')];

    this.tabsContainer.style.setProperty('--duration', this.SLIDESHOW_INTERVAL_DURATION + 'ms');

    this.toggleSlide(0);
    this.attachToggleEvents();

    // Initialize auto-slideshow on desktops
    if (window.innerWidth >= MOBILE_BREAKPOINT) {

      // todo: stop tab animation after interaction
      // todo: pause tab css animation on hover
      // todo: card toggle timeout on hover

      //this.attachSlideshowEvents();
      //this.startSlideShow();
    }
  }

  attachToggleEvents(){
    this.tabs.forEach(tab => {
      tab.addEventListener('click', (e) => this.onTabClick(e, tab));
    });
    this.cards.forEach(card => {
      card.addEventListener('click', (e) => this.onCardClick(e, card));
    });
  }

  onTabClick(e, tab){
    this.hasInteracted = true;
    this.stopSlideShow();

    const isActive = tab.classList.contains(this.CLASS_TAB_ACTIVE);
    if (!isActive) {
      const index = this.tabs.indexOf(tab);
      this.toggleSlide(index);
    }
  }

  onCardClick(e, card){
    this.hasInteracted = true;
    this.stopSlideShow();

    const isActive = card.classList.contains(this.CLASS_CARD_ACTIVE);
    if (!isActive) {
      const index = this.cards.indexOf(card);
      this.toggleSlide(index);
    }
  }

  toggleSlide(index){
    this.cards.forEach(card => card.classList.remove(this.CLASS_CARD_ACTIVE));
    this.cards[index].classList.add(this.CLASS_CARD_ACTIVE);

    this.tabs.forEach(tab => tab.classList.remove(this.CLASS_TAB_ACTIVE));
    this.tabs[index].classList.add(this.CLASS_TAB_ACTIVE);

    this.cardsContainer.style.setProperty('--slide', index);
  }

  attachSlideshowEvents(){
    this.cards.forEach(card => {
      card.addEventListener('mouseenter', () => this.stopSlideShow());
      card.addEventListener('mouseleave', () => this.startSlideShow());
    });
  }

  startSlideShow(){
    if (this.hasInteracted) {
      return;
    }
    if (this.slideshowInterval) {
      this.stopSlideShow();
    }

    this.slideshowInterval = setInterval(() => {
      const nextIndex = this.getSlideshowSlideNextIndex();
      this.toggleSlide(nextIndex);
    }, this.SLIDESHOW_INTERVAL_DURATION);
  }

  stopSlideShow(){
    if (this.hasInteracted) {
      return;
    }
    clearInterval(this.slideshowInterval);
    this.slideshowInterval = null;
  }

  getSlideshowSlideNextIndex(){
    const currentIndex = this.cards.findIndex(card => card.classList.contains(this.CLASS_CARD_ACTIVE));
    return currentIndex === this.cards.length - 1
      ? 0
      : currentIndex + 1;
  }
}
