import 'bootstrap/js/dist/dropdown';
import 'bootstrap/js/dist/modal';

import { HeroAnimation } from './HeroAnimation';
import { BusinessesCards } from './BusinessesCards';
import { Globe } from './Globe';
import { MOBILE_BREAKPOINT } from './Config';

const businessesCards = new BusinessesCards();
const heroAnimation = new HeroAnimation();
const globe = new Globe();

window.TA = { heroAnimation, businessesCards, globe };


/**
 * "Our Kiosks in Action" card -> "Learn More" popup
 */

document.querySelector('.KioskLivePopup').addEventListener('shown.bs.modal', () => {
  togglePopupVideoPlayback(true);
});

document.querySelector('.KioskLivePopup').addEventListener('hidden.bs.modal', () => {
  togglePopupVideoPlayback(false);
});

function togglePopupVideoPlayback(shouldPlay) {
  const isMobile = window.innerWidth < MOBILE_BREAKPOINT;
  const selector = isMobile ? '.KioskLivePopup__video--mobile' : '.KioskLivePopup__video--desktop';
  const video = document.querySelector(selector);

  video.currentTime = 0;
  shouldPlay ? video.play() : video.pause();
}


/**
 * "Memorable Souvenir" card
 */

function incrementSouvenirReactionsCount(){
  const DEFAULT_COUNT = 1255;
  const INTERVAL_FROM = 0;
  const INTERVAL_TO = 3000;

  const container = document.querySelector('.Kiosks__souvenir-reaction-count');
  const interval = Math.floor(Math.random() * (INTERVAL_TO - INTERVAL_FROM + 1)) + INTERVAL_FROM;

  setTimeout(() => {
    const count = +container.textContent;
    container.textContent = count ? count + 1 : DEFAULT_COUNT;
    incrementSouvenirReactionsCount();
  }, interval);
}

incrementSouvenirReactionsCount();
