export class HeroAnimation {

  portrait;

  scrollStartPos;
  scrollEndPos;
  scrollHeight;

  isAnimating = false;

  constructor(){
    this.portrait = document.getElementById('hero-animation-portrait');
    this.scrollStartPos = this.getTopOffset(document.getElementById('hero-animation-start'));
    this.scrollEndPos = this.getTopOffset(document.getElementById('hero-animation-end'));
    this.scrollHeight = this.scrollEndPos - this.scrollStartPos;

    this.animatePhotoPortraitSwap(this.getAnimationProgress());
    this.animatePhotoRotation(this.getAnimationProgress());
    this.animateTagsTransform(this.getAnimationProgress());

    this.onAnimationProgress(progress => {
      this.animatePhotoPortraitSwap(progress);
      this.animatePhotoRotation(progress);
      this.animateTagsTransform(progress);
    });
  }

  onAnimationProgress(callback){
    window.addEventListener('scroll', () => {
      const progress = this.getAnimationProgress();

      if (this.isAnimating) {
        callback(progress);
      }
      this.isAnimating = progress !== 0 && progress !== 1;
    });
  }

  getAnimationProgress(){
    const progressHeight = window.scrollY - this.scrollStartPos;
    return Math.max(Math.min(progressHeight / this.scrollHeight, 1), 0);
  }

  getTopOffset(element) {
    return element.getBoundingClientRect().top + window.scrollY;
  }

  animatePhotoPortraitSwap(progress){
    this.portrait.style.width = (progress * 100) + '%';
  }

  animatePhotoRotation(progress){
    // todo: implement
  }

  animateTagsTransform(progress){
    // todo: implement
    // todo: separate for desktop & mobile?
  }
}
