import { v4 as uuidv4 } from 'uuid';
import { LOCATION_CITIES } from './Config';

export class Globe {

  globeContainer;
  globe;
  counterLocations;
  counterCountries;

  markers = [];
  isAnimatingMarkers = false;

  CLASSNAME_ACTIVE = 'active';

  MARKER_TOGGLE_INTERVAL = 5000;
  COUNTER_INCREMENT_DURATION = 2500;

  constructor(){
    this.initialize();
  }

  async initialize(){
    this.globeContainer = document.querySelector('.Locations__globe');
    this.counterLocations = document.querySelector('.Locations__counter-value--locations');
    this.counterCountries = document.querySelector('.Locations__counter-value--countries');

    // Dynamically load threejs-earth.js
    if (!window.Earth) {
      await this.loadEarthLibrary();
    }

    if (!window.Earth) {
      console.error('Earth library failed to load');
      return;
    }

    this.globe = await this.renderGlobe(this.globeContainer);
    this.markers = await this.renderMarkers();
    this.launchMarkerAnimation();
    this.initializeCounters();
  }

  loadEarthLibrary() {
    return new Promise((resolve, reject) => {
      const script = document.createElement('script');
      script.src = '/public/threejs-earth.js';
      script.onload = () => {
        if (window.Earth) {
          resolve();
        } else {
          reject(new Error('Earth not defined after script load'));
        }
      };
      script.onerror = () => reject(new Error('Failed to load threejs-earth.js'));
      document.head.appendChild(script);
    });
  }

  initializeCounters(){
    const countryCount = new Set(LOCATION_CITIES.map(location => location.country.flag)).size;
    const locationCount = LOCATION_CITIES
      .map(location => location.experiences.length)
      .reduce((acc, count) => acc + count, 0);

    const observer = new IntersectionObserver(entries => {
      if (entries[0].isIntersecting) {
        this.animateCounter(this.counterCountries, countryCount);
        this.animateCounter(this.counterLocations, locationCount);
        observer.disconnect();
      }
    }, { threshold: 0.8 });

    observer.observe(this.globeContainer);
  }

  animateCounter(el, targetValue, duration = this.COUNTER_INCREMENT_DURATION){
    const startTime = performance.now();
    const startValue = 0;

    const update = currentTime => {
      const progress = Math.min(1, (currentTime - startTime) / duration);
      el.textContent = Math.round(startValue + (targetValue - startValue) * progress);

      if (progress < 1){
        requestAnimationFrame(update);
      }
    };
    requestAnimationFrame(update);
  }

  launchMarkerAnimation(){
    this.isAnimatingMarkers = true;
    this.animateMarkers();
  }

  stopMarkerAnimation(){
    this.isAnimatingMarkers = false;
  }

  async animateMarkers(index = 0){
    if (!this.isAnimatingMarkers) {
      return;
    }
    if (index < 0 || index >= this.markers.length) {
      index = 0;
    }

    const marker = this.markers[index];
    await this.toggleActiveMarker(marker);

    return new Promise(resolve => {
      setTimeout(() => {
        resolve(this.animateMarkers(index + 1));
      }, this.MARKER_TOGGLE_INTERVAL);
    });
  }

  renderGlobe(container){
    const globe = new window.Earth(container, {
      location : { lat: 20, lng: 20 },
      light: 'none',
      zoom: 10,
      mapImage: '/public/globe-texture.svg',
      transparent: true,
      autoRotateSpeed: 0,
    });

    return new Promise(resolve => {
      globe.addEventListener('ready', () => {
        resolve(globe);
      });
    });
  }

  renderMarkers(){
    const markers = LOCATION_CITIES.map(location => {
      const uuid = uuidv4();
      const overlay = this.globe.addOverlay({
        location: {
          lat: location.lat,
          lng: location.lng,
        },
        content: this.renderMarkerHtml(location, uuid),
        depthScale: 0.3,
      });
      return { uuid, overlay, location };
    });

    return new Promise(resolve => {
      requestAnimationFrame(() => {
        markers.forEach(marker => {
          marker.overlay.element.addEventListener('click', (e) => {
            this.stopMarkerAnimation();
            this.toggleActiveMarker(marker);
          });
        });
        resolve(markers);
      });
    });
  }

  renderMarkerHtml(location, uuid){
    const { city, region, country, experiences } = location;
    const { markerCaptionTemplate1, markerCaptionTemplate2 } = this.globeContainer.dataset;

    const countryName = country[localStorage.ta_lang] || country.en;
    const titleFull = [city, region, countryName].filter(Boolean).join(', ');
    const titleShort = [city, region].filter(Boolean).join(', ');

    const experiencesCaption = experiences.length === 1
      ? experiences[0]
      : experiences[0] + ` ${markerCaptionTemplate1} ${experiences.length - 1} ${markerCaptionTemplate2}`;

    return `
      <div class="Locations__marker" data-uuid="${uuid}">
        <div class="Locations__marker-balloon">
          <div class="Locations__marker-title ta-hide-mobile">${titleFull} ${country.flag}</div>
          <div class="Locations__marker-caption ta-hide-mobile">${experiencesCaption}</div>
          <div class="Locations__marker-title ta-hide-desktop">${titleShort}</div>
          <div class="Locations__marker-caption ta-hide-desktop">
            ${countryName} <span class="Locations__marker-caption-flag">${country.flag}</span>
          </div>
        </div>
        <div class="Locations__marker-pin"></div>
      </div>
    `;
  }

  toggleActiveMarker(marker){
    this.markers.forEach(marker => {
      marker.overlay.element.classList.remove(this.CLASSNAME_ACTIVE);
    });
    marker.overlay.element.classList.add(this.CLASSNAME_ACTIVE);
    return this.scrollToMarker(marker);
  }

  scrollToMarker(marker){
    return new Promise(resolve => {
      this.globe.goTo(marker.location, {
        relativeDuration: 100,
        complete: () => resolve(),
      });
    });
  }
}
